import { useState } from "react";
import { Button, ButtonGroup, Checkbox, Form, Input, Modal } from "semantic-ui-react";
import "./Todos.css";

import React from "react";
import { TestsAreDemoCheat } from "../../lib";
import { colorHigh, colorLow, colorNormal, Priority, Todo, todosTestids } from "./Todos";

interface TodoFormProps {
    todo?: Todo;
    editedTodoIndex: number;
    onSave: (editedTodo: Todo) => void;
    onClose: () => void;
}

export const TodoForm = (props: TodoFormProps) => {
    const [editedTodo, setEditedTodo] = useState<Todo>(props.todo || { text: "" });
    return (<Modal open closeIcon closeOnDimmerClick={false} onClose={props.onClose}>
        <Modal.Header>{props.editedTodoIndex >= 0 ? "Edit" : "Add"} Todo</Modal.Header>
        <Modal.Content>
            <Form data-testid={todosTestids.form}>
                <Form.Field>
                    <label>Todo text</label>
                    <Input data-testid={todosTestids.inputText} autoFocus value={editedTodo.text} onChange={(_, data) => setEditedTodo({ ...editedTodo, text: data.value })} />
                </Form.Field>
                <Form.Field>
                    <Checkbox data-testid={todosTestids.formCheckbox} label='Done' checked={editedTodo.done} onChange={() => setEditedTodo({ ...editedTodo, done: !editedTodo.done })} />
                </Form.Field>
                <Form.Field>
                    <label>Percentage done</label>
                    <Slider value={editedTodo.percentageDone} onChange={newValue => setEditedTodo({ ...editedTodo, percentageDone: newValue })} />
                </Form.Field>
                <Form.Field>
                    <label>Priority</label>
                    <ButtonGroup data-testid={todosTestids.formPriority}>
                        <Button color={colorLow} content="Low" basic={editedTodo.priority !== Priority.LOW} onClick={() => setEditedTodo({ ...editedTodo, priority: Priority.LOW })} />
                        <Button color={colorNormal} content="Normal" basic={!(!editedTodo.priority)} onClick={() => setEditedTodo({ ...editedTodo, priority: Priority.NORMAL })} />
                        <Button color={colorHigh} content="High" basic={editedTodo.priority !== Priority.HIGH} onClick={() => setEditedTodo({ ...editedTodo, priority: Priority.HIGH })} />
                    </ButtonGroup>
                </Form.Field>
            </Form>
        </Modal.Content>
        <Modal.Actions>
            <Button data-testid={todosTestids.save} positive content="Save" onClick={() => { props.onSave(editedTodo); props.onClose() }} />
            <Button data-testid={todosTestids.cancel} content="Cancel" onClick={props.onClose} />
        </Modal.Actions>
    </Modal>)
}

export class Slider extends React.Component<{ value?: number, onChange: (newValue: number) => void }> {

    onChange(newValue: string) {
        this.props.onChange(Number(newValue));
    }

    render() {
        return <>
            <TestsAreDemoCheat objectToPublish={this} />
            <input type="range" min={0} max={100} step={10} value={this.props.value || 0} onChange={v => this.onChange(v.target.value)} />
        </>
    }
}