import { useState } from "react";
import { Button, Checkbox, Modal, Segment } from "semantic-ui-react";

import { TestsAreDemoCheat } from "../../lib";
import { colorHigh, colorLow, colorNormal, Priority, Todo, todosTestids } from "./Todos";

interface TodoItemProps {
    index: number;
    todo: Todo;
    onToggle: () => void;
    onUp: () => void;
    onDown: () => void;
    onEdit: () => void;
    onDelete: () => void;
}

// Because TodoItem is not a class component, we cannot expose it via "cheat". We need an actual
// class for this. 
export class TodoItemCheat {

    // We may consider this an "abstract" function
    getTodo!: () => Todo;
}

export const TodoItem = ({ todo, ...props }: TodoItemProps) => {
    const [modalOpen, setModalOpen] = useState(false);
    return (<Segment data-testid={todosTestids.todoItem + "_" + props.index} onDoubleClick={props.onEdit} className="TodoItem" color={!todo.priority ? colorNormal : todo.priority === Priority.HIGH ? colorHigh : colorLow}>
        <TestsAreDemoCheat objectToPublish={new class extends TodoItemCheat {
            getTodo = () => todo;
        }()} />
        <div data-testid={todosTestids.todoItemCheckbox} className="TodoItem_left">
            <Checkbox onChange={props.onToggle} label={<label data-testid={todosTestids.todoItemCheckboxLabel} style={{ textDecoration: todo.done ? "line-through" : "" }} >{todo.text}</label>} checked={todo.done ? true : false} />
        </div>
        <div>
            {todo.percentageDone || 0} % done &nbsp;
            <Button data-testid={todosTestids.up} onClick={props.onUp} basic icon="arrow up" />
            <Button data-testid={todosTestids.down} onClick={props.onDown} basic icon="arrow down" />
            <Button data-testid={todosTestids.edit} onClick={props.onEdit} basic icon="edit" color="yellow" content="Edit" />
            <Button data-testid={todosTestids.delete} onClick={() => setModalOpen(true)} basic icon="delete" color="red" content="Delete" />
        </div>
        {modalOpen && <Modal open content="Are you sure you want to delete?" onClose={() => setModalOpen(false)} actions={[
            { "data-testid": todosTestids.deleteYes, key: "yes", content: "Yes", positive: true, onClick: () => { props.onDelete(); setModalOpen(false); } },
            { "data-testid": todosTestids.deleteNo, key: "no", content: "No", onClick: () => setModalOpen(false) }
        ]} />}
    </Segment>)
}