import { useEffect, useState } from "react";
import { Button, Container, Label, Segment, SegmentGroup } from "semantic-ui-react";
import "./Todos.css";

import HttpRequestMock from 'http-request-mock';
import { createTestids } from "../../lib";
import { TodoForm } from "./TodoForm";
import { TodoItem } from "./TodoItem";

/**
 * NORMAL is on purpose the first; to have index 0; and to be compatible w/ undefined
 * To be able to say IF !priority THEN it means priority is normal
 */
export enum Priority { NORMAL, LOW, HIGH }

export const colorNormal = "blue";
export const colorLow = "yellow";
export const colorHigh = "red";

export interface Todo {
    text: string;
    done?: boolean;
    percentageDone?: number;
    priority?: Priority;
}

// TODO: experimental; how should we recommend to write such "constants"? They look a lot like the EMF metamodel & stuff.
// we should reuse them also when we work w/ fields in code, e.g. to create a filter.
// And we should have something similar in Java.
// Should we create a generator? Not APT :(, but plain generators.
// And maybe we could make a single value contain both entity + field, so that we can call
// the API w/ only one param, not 2. E.g. tad.miniDb.get(entities.Todo.text, 1)
export const entities = {
    Todo: "Todo"
}

export const fields = {
    Todo: {
        // TODO experimental: at least like we have for testids, where we input "", to avoid copying the prop name
        text: "text",
        done: "done",
        priority: "priority" 
    }
}

// TODO: experimental; when we finish the migration, we should get rid of this constant. We should
// "hide" it in the mocked services. Normally, such values come from the server.
export const someTodos = [
    { id: 1, text: "Buy groceries for next week", priority: Priority.HIGH, done: true },
    { id: 2, text: "Repair the bike", priority: Priority.HIGH },
    { id: 3, text: "Prepare birthday cake" },
    { id: 4, text: "Buy birthday present" },
    { id: 5, text: "Change tyres (for winter)" },
    { id: 6, text: "Wash the car", priority: Priority.LOW },
];

const mocker = HttpRequestMock.setup();
mocker.get('https://my.server.com/getUserId', '{ "userId": 1234 }', { delay: 100 });
mocker.get('https://my.server.com/getTodosForUser/1234', JSON.stringify(someTodos), { delay: 100 });
for (let i = 1; i <= someTodos.length; i++) {
    mocker.get('https://my.server.com/getTodosForUser/1234/' + i, JSON.stringify(someTodos[i - 1]), { delay: 100 });
}

function copyTodosAndReplace(todos: Todo[], index: number, modifiedValue: Todo) {
    const newTodos = [...todos];
    if (index === -1) {
        newTodos.push(modifiedValue);
    } else {
        newTodos[index] = modifiedValue;
    }
    return newTodos;
}

function copyTodosAndMoveUp(todos: Todo[], index: number) {
    const newTodos = [...todos];
    if (index >= 1) {
        newTodos[index - 1] = todos[index];
        newTodos[index] = todos[index - 1];
    }
    return newTodos;
}

function copyTodosAndMoveDown(todos: Todo[], index: number) {
    const newTodos = [...todos];
    if (index <= todos.length - 2) {
        newTodos[index + 1] = todos[index];
        newTodos[index] = todos[index + 1];
    }
    return newTodos;
}

function copyTodosAndDelete(todos: Todo[], index: number) {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    return newTodos;
}

export const todosTestids = createTestids("Todos", {
    total: "", totalLow: "", totalNormal: "", totalHigh: "",
    todoItem: "", todoItemCheckbox: "", todoItemCheckboxLabel: "",
    add: "",

    // TodoItem
    up: "", down: "", edit: "", delete: "",
    deleteYes: "", deleteNo: "",

    // TodoForm
    form: "", inputText: "", formCheckbox: "", formPriority: "",
    save: "", cancel: "",
});

export const Todos = () => {
    const [todos, setTodos] = useState<Todo[]>([]);
    // <= -2 => form closed; -1 => form open, "add" mode; >=0 => form open, "edit" mode
    const [editedTodoIndex, setEditedTodoIndex] = useState(-2);

    useEffect(() => {
        (async () => { // cannot pass an async func to useEffect()
            const user = await fetch("https://my.server.com/getUserId").then(r => r.json());
            setTodos(await fetch("https://my.server.com/getTodosForUser/" + user.userId).then(r => r.json()));
        })();
    }, []);

    let noLow = 0, noNormal = 0, noHigh = 0;
    for (let todo of todos) {
        if (todo.priority === Priority.LOW) {
            noLow++;
        } else if (todo.priority === Priority.HIGH) {
            noHigh++;
        } else {
            noNormal++;
        }
    }
    return <Container>
        <SegmentGroup>
            {todos.map((todo, i) => <TodoItem key={i} index={i} todo={todo}
                onToggle={() => setTodos(copyTodosAndReplace(todos, i, { ...todo, done: !todo.done }))}
                onUp={() => setTodos(copyTodosAndMoveUp(todos, i))}
                onDown={() => setTodos(copyTodosAndMoveDown(todos, i))}
                onEdit={() => { setEditedTodoIndex(i); }}
                onDelete={() => setTodos(copyTodosAndDelete(todos, i))}
            />)}
        </SegmentGroup>
        <Segment className="TodoItem">
            <div className="TodoItem_left">
                <Button data-testid={todosTestids.add} icon="add" primary content="Add" onClick={() => { setEditedTodoIndex(-1); }} />
            </div>
            <div>
                <Label>Total: <span data-testid={todosTestids.total}>{todos.length}</span></Label>
                &nbsp; By priority &nbsp;
                <Label color={colorLow}>Low: <span data-testid={todosTestids.totalLow}>{noLow}</span></Label>
                <Label color={colorNormal}>Normal: <span data-testid={todosTestids.totalNormal}>{noNormal}</span></Label>
                <Label color={colorHigh}>High: <span data-testid={todosTestids.totalHigh}>{noHigh}</span></Label>
            </div>
        </Segment>
        {editedTodoIndex >= -1 && <TodoForm todo={todos[editedTodoIndex]} editedTodoIndex={editedTodoIndex}
            onClose={() => setEditedTodoIndex(-2)}
            onSave={editedTodo => setTodos(copyTodosAndReplace(todos, editedTodoIndex, editedTodo))}
        />}
    </Container>
}

